label {
  display: block;
  color: var(--darker-gray-color);
  font-size: 1.4rem;
  font-weight: 700;
}

label + label {
  margin-top: 2rem;
}

label input,
label textarea {
  margin-top: 0.7rem;
}

input,
textarea {
  padding: 0 1.5rem;
  width: 100%;
  height: 44px;
  outline: none;
  border: 1px solid var(--light-gray-color);
  color: var(--darker-gray-color);
  font-size: 1.6rem;
  background-color: var(--white-color);
  border-radius: 5px;
  appearance: none;
}

input:focus,
textarea:focus {
  border-color: var(--mid-gray-color);
}

textarea {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  height: 100%;
  resize: vertical;
}

.form-wrapper {
  position: relative;
}

.form-button {
  position: absolute;
  top: 5px;
  right: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 40px;
  height: 40px;
  outline: none;
  border: 0;
  color: var(--white-color);
  background-color: var(--brand-color);
  border-radius: 0 4px 4px 0;
  cursor: pointer;
}

.form-button .icon-loading {
  display: none;
}

.loading .form-button .icon {
  display: none;
}

.loading .form-button .icon-loading {
  display: block;
}

.contact-form {
  margin-top: 3rem;
}

.contact-form .button {
  margin-top: 1.5rem;
}
